import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { Camera } from '@ionic-native/camera';

import { MyApp } from './app.component';

import { QuestionPaperGenratorPage } from '../pages/question-paper-genrator/question-paper-genrator';
import { BlueprintPage } from '../pages/blueprint/blueprint';
import { AboutusPage } from '../pages/aboutus/aboutus';
import { SettingsPage } from '../pages/settings/settings';


import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { SettingsEditPage } from '../pages/settings-edit/settings-edit';

@NgModule({
  declarations: [
    MyApp,
    QuestionPaperGenratorPage,
    BlueprintPage,
    AboutusPage,
    SettingsPage,
    SettingsEditPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp,{
      backButtonText: ''
    }), 
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    QuestionPaperGenratorPage,
    BlueprintPage,
    AboutusPage,
    SettingsPage,
    SettingsEditPage
  ],
  providers: [
    StatusBar,
     Camera,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



